import {motion} from 'framer-motion'
import {
    flowerAndFrameAnimation,
    mainLineAnimation,
    subLineAnimation,
    polygonAnimation,
    dotAnimation,
    textAnimation, basicfahmAnimation,
} from '../../../animations/fahm';
import {
    backgroundAnimationHazmMobile,
    bottomLineAnimationHazmMobile,
    boxAnimationHazmMobile,
    aboutAnimationHazmMobile,
} from '../../../animations/hazm';
import {noneAnimation} from "../../../animations/main-page";
import {flowerAnimation} from "../../../animations/fahm-image";

const animationChooser = (scrollQuantity) => {

    switch (scrollQuantity) {
        case 4:
            return {
                'flowerAndFrameAnimation':flowerAndFrameAnimation,
                'mainLineAnimation': mainLineAnimation,
                'subLineAnimation': subLineAnimation,
                'polygonAnimation': polygonAnimation,
                'dotAnimation': dotAnimation,
                'textAnimation': textAnimation,
                'flowerAnimation':flowerAnimation,
                'backgroundAnimationHazmMobile': backgroundAnimationHazmMobile,
                'bottomLineAnimationHazmMobile': bottomLineAnimationHazmMobile,
                'boxAnimationHazmMobile': boxAnimationHazmMobile,
                'aboutAnimationHazmMobile': aboutAnimationHazmMobile,
                'basicfahmAnimation':basicfahmAnimation,
            };

        default:
            return {
                'flowerAndFrameAnimation':noneAnimation,
                'mainLineAnimation': noneAnimation,
                'subLineAnimation': noneAnimation,
                'polygonAnimation': noneAnimation,
                'dotAnimation': noneAnimation,
                'textAnimation': noneAnimation,
                'flowerAnimation':noneAnimation,
                'backgroundAnimationHazmMobile': noneAnimation,
                'bottomLineAnimationHazmMobile': noneAnimation,
                'boxAnimationHazmMobile': noneAnimation,
                'aboutAnimationHazmMobile': noneAnimation,
                'basicfahmAnimation':noneAnimation,
            };
    }
};

